@mixin mobile() {
  @media screen and (max-width: #{$mobile-breakpoint}) {
    @content;
  }
}

@mixin large-screen() {
  @media screen and (min-width: #{$large-screen-breakpoint}) {
    @content;
  }
}

@mixin print() {
  @media print {
    @content;
  }
}
