.archive-main {
  display: flex;

  a {
    color: $dark-5;
    text-decoration: none;

    &:hover {
      text-decoration: underline;
    }
  }

  .archive-section {
    width: 50%;
  }

  .archive-item {
    line-height: 1.8rem;
    margin-top: 0.6rem;
    overflow: auto;

    .archive-date {
      color: $bright;
      font-size: 0.8rem;
      margin: 0;
    }
  }
}

.archive-more {
  color: $dark-5;
  margin: 0.8rem 0;

  a {
    color: $dark-5;
  }
}
